html,
body,
#container{
  height: 100%;
  width: 100%;
  margin: 0;    
}
.content{
  padding: 30px;
  width: 100%;
}
body{
  background-image: url('./wallpapers/wallpaper1.jpg');
  -webkit-background-size: cover;
-moz-background-size: cover;
-o-background-size: cover;
background-size: cover;
  background-color: #282828;
  color: white;
  font-family: 'Segoe UI';
}
.flex{
  display: flex;
}
.column {
  flex-direction: column;
}
.row{    
  flex-direction: row;
}
.center {
  justify-content: center;
  align-items: center;
}
.link{
  color: white;
  padding: 0 8px;
  pointer-events: all !important;
}
.small.btn {
  font-size: 1em;
}
.btn{
  cursor: pointer;
  display: flex;
  width: fit-content;
  padding: 5px 20px;
  border: 3px solid white;
  color: white;
  text-decoration: none;
  position: relative;
  z-index: 1;
  margin: 15px;
  font-weight: 600;
  background: rgba(255, 255, 255, .35);
  transform: skewX(345deg);
  font-size:1.5em;
}
.btn:hover .icon{
  width: 35px;
}
.btn .icon {
  letter-spacing: -4px;
  margin: 0 0 0 10px;
  transition: width .5s;
  width: 0;
  overflow: hidden;
}
.btn:before{
  content: '';
  border: 1px solid red;
  width: calc(100% + 5px);
  position: absolute;
  height: calc(100% + 5px);
  top: 3px;
  left: 3px;
  border: 3px solid white;
  border-left: none;
  z-index: 0;
  border-top: none;
}
#container {
  text-align: center;
  display: flex;
}
input[type="text"], select {
  height: 1.3em;
  padding: 3px 10px;
  border-width: 1px;
}
select {
  height: 1.9em;
  max-width: 20em;
}
input[type="checkbox"]{
  width: 1.2em;
  height: 1.2em;
}